/*=====================
    1.General CSS start
==========================*/
body {
	font-family: $font_0, $font_1;
	position: relative;
	background: $white;
	font-size: 14px;
	overflow-x: hidden;
	letter-spacing: 0.8px;
}
h1 {
	font-size: 60px;
	color: $dark-font;
	font-weight: 700;
	text-transform: uppercase;
	span {
		font-size: 107px;
		font-weight: 700;
		color: $theme-deafult;
	}
}
h2 {
	font-size: 36px;
	color: $dark-font;
	text-transform: uppercase;
	font-weight: 700;
	line-height: 1;
	letter-spacing: 0.02em;
}
h3 {
	font-size: 24px;
	font-weight: 400;
	color: $grey;
	letter-spacing: 0.03em;
}
h4 {
	font-size: 18px;
	text-transform: capitalize;
	font-weight: 400;
	letter-spacing: 0.03em;
	line-height: 1;
}
h5 {
	font-size: 16px;
	font-weight: 400;
	color: $dark-font;
	line-height: 24px;
	letter-spacing: 0.05em;
}
h6 {
	font-size: 14px;
	font-weight: 400;
	color: $grey;
	line-height: 24px;
}
ul {
	padding-left: 0;
	margin-bottom: 0;
}
li {
	display: inline-block;
}
p {
	font-size: 14px;
	color: $grey;
	line-height: 1;
}
a {
	transition: 0.5s ease;
	&:hover {
		text-decoration: none;
		transition: 0.5s ease;
	}
	&:focus {
		outline: none;
	}
}
button {
	&:focus {
		outline: none;
	}
}
.white {
	background-color: #fff;
	border: 1px solid #aaa;
}
.blue {
	background-color: #4695b1;
}
.pink {
	background-color: pink;
}
.yellow {
	background-color: #fdf791;
}
.red {
	background-color: #b65d60;
}
.gray {
	background-color: #bababa;
}
.green {
	background-color: #7ee768;
}
.orange {
	background-color: rgba(230, 184, 71, 0.92);
}
.black {
	background-color: #1b1b1b;
}
section,
.section-t-space {
