}
.icon-angle-left:before {
	content: "\E64A";
}
.form-control {
	border-radius: 0;
}
.small-section {
	padding-top: 35px;
	padding-bottom: 35px;
}
.banner-padding {
	padding-top: 30px;
}
.border-section {
	border-top: 1px solid lighten($black, 86.5%);
	border-bottom: 1px solid lighten($black, 86.5%);
}
.border-b {
	border-bottom: 1px solid $border-color;
}
.border-bottom-grey {
	border-bottom: 1px solid $grey3;
}
.border-top-grey {
	border-top: 1px solid $grey3;
}
.darken-layout {
	background-color: $grey-darken;
}
.dark-layout {
	background-color: $grey-dark;
}
.light-layout {
	background-color: $grey-lighter;
}
.white-layout {
	background-color: $white;
}
.bg-light0 {
	background-color: $light-grey;
}
.bg-light1 {
	background-color: $pink;
}
.bg-light2 {
	background-color: $blue;
}
.bg-blog {
	background-color: $white-dark;
}
.Toastify__toast {
	min-height: 36px;
}
.input-range__track--active {
	background: $theme-deafult !important;
}
.input-range__slider {
	background: $theme-deafult !important;
	border: 1px solid $theme-deafult !important;
}
.block-price-content {
	.custom-control {
		padding-right: 1.5rem;
		padding-bottom: 18px;
	}
}
del {
	font-size: 14px;
	color: $font-grey;
	font-weight: 400;
}
.sidenav .sidebar-back {
	display: flex;
	align-items: center;
}
.srv-validation-message {
	color: red;
}
.sticky {
	&.fixed {
		position: fixed;
		width: 100%;
		top: 0;
		z-index: 999;
		transition: all 300ms ease;
		background: #fff;
		box-shadow: 0px 0px 8px #ddd;
		animation: smoothScroll 1s forwards;
		.brand-logo {
			padding-top: 15px;
			padding-bottom: 15px;
		}
		.onhover-div {
			.show-div {
				top: 66px;
			}
		}
		.pixelstrap > li > a,
		.pixelstrap > li > a:hover,
		.pixelstrap > li > a:active {
		}
		.onhover-div {
		}

		.top-header {
			display: none;
		}
		.cart-qty-cls {
			top: 13%;
			right: -11px;
		}
	}
}
@-webkit-keyframes smoothScroll {
	0% {
		-webkit-transform: translateY(-40px);
		transform: translateY(-40px);
	}
	100% {
		-webkit-transform: translateY(0px);
		transform: translateY(0px);
	}
}
@keyframes smoothScroll {
	0% {
		-webkit-transform: translateY(-40px);
		transform: translateY(-40px);
	}
	100% {
		-webkit-transform: translateY(0px);
		transform: translateY(0px);
	}
}
.shopping-cart {
	&::-webkit-scrollbar {
		width: 3px;
	}

	/* Track */
	&::-webkit-scrollbar-track {
		background: #ddd;
	}

	/* Handle */
	&::-webkit-scrollbar-thumb {
		background: $theme-deafult;
		border-radius: 10px;
	}

	/* Handle on hover */
	&::-webkit-scrollbar-thumb:hover {
		background: #555;
	}
}
[data-notify="progressbar"] {
	margin-bottom: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 5px;
}
.progress-bar {
	background-color: #19a340;
}
.progress-bar-info {
	background-color: #00829a;
}
