/*=====================
    1.1.Button CSS start
==========================*/
button {
	cursor: pointer;
}
.btn {
	line-height: 20px;
	text-transform: uppercase;
	font-size: 14px;
	font-weight: 700;
	border-radius: 0;
	transition: 0.3s ease-in-out;
	&:hover {
		transition: 0.3s ease-in-out;
	}
	&:focus {
		box-shadow: none;
	}
}
.btn-solid {
	padding: 13px 29px;
	color: $white !important;
	letter-spacing: 0.05em;
	border: 2px solid $theme-deafult;
	background-image: -webkit-linear-gradient(
		30deg,
		$theme-deafult 50%,
		transparent 50%
	);
	background-image: linear-gradient(30deg, $theme-deafult 50%, transparent 50%);
	background-size: 540px;
	background-repeat: no-repeat;
	background-position: 0;
	-webkit-transition: background 300ms ease-in-out;
	transition: background 300ms ease-in-out;
	&:hover {
		background-position: 100%;
		color: $black !important;
		background-color: $white;
	}
	&.black-btn {
		background-image: -webkit-linear-gradient(
			30deg,
			$dark-font 50%,
			transparent 50%
		);
		background-image: linear-gradient(30deg, $dark-font 50%, transparent 50%);
		border: 2px solid $dark-font;
	}
	&:focus {
		color: $white;
	}
}
.btn-outline {
	display: inline-block;
	padding: 13px 29px;
	letter-spacing: 0.05em;
	border: 2px solid $theme-deafult;
	position: relative;
	color: $black;
	&:before {
		-webkit-transition: 0.5s all ease;
		transition: 0.5s all ease;
		position: absolute;
		top: 0;
		left: 50%;
		right: 50%;
		bottom: 0;
		opacity: 0;
		content: "";
		background-color: $theme-deafult;
		z-index: -2;
	}
	&:hover {
		color: $white;
		&:before {
			-webkit-transition: 0.5s all ease;
			transition: 0.5s all ease;
			left: 0;
			right: 0;
			opacity: 1;
		}
	}
	&:focus {
		&:before {
			transition: 0.5s all ease;
			left: 0;
			right: 0;
			opacity: 1;
		}
	}
}
button {
	&.btn {
		&.btn-solid,
		&.btn-outline {
			&:active {
				background-image: -webkit-linear-gradient(
					30deg,
					$theme-deafult 50%,
					transparent 50%
				);
				background-image: linear-gradient(
					30deg,
					$theme-deafult 50%,
					transparent 50%
				);
				color: $white;
				background: $theme-deafult;
			}
		}
	}
}
.btn-classic {
	&:hover {
		background-color: $theme-deafult;
	}
}
.btn-theme {
	background-color: $theme-deafult;
	color: $white;
}

