/*=====================
    1.3.Brand logo CSS start
    ==========================*/
.logo-block {
	a {
		&:focus {
			outline: none;
		}
	}

	img {
		filter: grayscale(100%);
		opacity: 0.5;
		transition: 0.5s ease;
		margin: 0 auto;

		&:focus {
			outline: none;
		}

		&:hover {
			filter: grayscale(0);
			opacity: 1;
			transition: 0.5s ease;
		}
	}
}

/*=====================
    2.Header CSS start
==========================*/
header {
	background-color: $white;
}
.onhover-dropdown {
	position: relative;
	font-size: 14px;
	&:before {
		content: "\f107";
		font-family: $font-awesome;
		position: absolute;
		right: 3px;
	}
	.onhover-show-div {
		top: 50px;
		position: absolute;
		z-index: 10;
		background-color: $white;
		transition: all linear 0.3s;
		min-width: 160px;
		text-align: left;
		-webkit-box-shadow: 0 0 1px 0 #ebebeb;
		-moz-box-shadow: 0 0 1px 0 #ebebeb;
		box-shadow: 0 0 1px 0 #ebebeb;
		transform: translateY(30px);
		opacity: 0;
		visibility: hidden;
		right: 0;
		padding: 15px 20px;
		border-radius: 0 !important;
		&.product-page-full {
			z-index: 99999;
		}
	}
	&:hover {
		.onhover-show-div {
			opacity: 1;
			transform: translateY(0px);
			visibility: visible;
		}
	}
}
.onhover-div {
	position: relative;
	padding-top: 40px;
	padding-bottom: 40px;
	font-family: $font_0, $font_1;
	img,
	i {
		cursor: pointer;
	}
	.show-div {
		top: 103px;
		position: absolute;
		z-index: 9;
		background-color: $white;
		transition: all linear 0.3s;
		min-width: 236px;
		text-align: left;
		-webkit-box-shadow: 0 0 1px 0 #ebebeb;
		-moz-box-shadow: 0 0 1px 0 #ebebeb;
		box-shadow: 0 0 1px 0 #ebebeb;
		transform: translateY(30px);
		opacity: 0;
		visibility: hidden;
		right: 0;
		padding: 0;
		border-radius: 0 !important;
	}
	&:hover {
		.show-div {
			opacity: 1;
			transform: translateY(0px);
			visibility: visible;
		}
	}
}
.search-overlay {
	height: 100%;
	width: 100%;
	display: none;
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	background-color: lighten($white, 5%);
	> div {
		height: 100%;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		.overlay-content {
			width: 100%;
			.form-control {
				&:focus {
					box-shadow: none;
				}
			}
			input {
				background-color: transparent;
				border: 0;
				border-bottom: 2px solid lighten($black, 46.5%);
				border-radius: 0;
				padding: 20px 0;
				&::-webkit-input-placeholder {
					/* Chrome/Opera/Safari */
					color: lighten($black, 46.5%);
					font-size: 18px;
				}
				&::-moz-placeholder {
					/* Firefox 19+ */
					color: lighten($black, 46.5%);
					font-size: 18px;
				}
				&:-ms-input-placeholder {
					/* IE 10+ */
					color: lighten($black, 46.5%);
					font-size: 18px;
				}
				&:-moz-placeholder {
					/* Firefox 18- */
					color: lighten($black, 46.5%);
					font-size: 18px;
				}
			}
			button {
				position: absolute;
				top: 10px;
				background-color: transparent;
				border: 0;
				font-size: 24px;
				color: lighten($black, 46.5%);
				right: 10px;
			}
		}
		.closebtn {
			position: fixed;
			top: 10px;
			right: 25px;
			color: lighten($black, 46.5%);
			font-size: 40px;
			cursor: pointer;
		}
	}
}
