/*=====================
    5.Blog CSS start
==========================*/
.blog {
	.slick-slide {
		line-height: 1;
	}
}
.blog-bg {
	background-color: $border-grey;
}
.classic-effect {
	position: relative;
	overflow: hidden;
	&:before,
	&:after {
		background: $theme-deafult;
		width: 25%;
		position: absolute;
		content: "";
		opacity: 0;
		-webkit-transition: all 0.3s steps(4);
		transition: all 0.3s steps(4);
		z-index: 1;
		bottom: 100%;
		top: 0;
	}
	&:before {
		left: 0;
		transition-delay: 0s;
	}
	&:after {
		left: 25%;
		transition-delay: 0.025s;
	}
	span {
		&:before,
		&:after {
			background: $theme-deafult;
			width: 25%;
			position: absolute;
			content: "";
			opacity: 0;
			-webkit-transition: all 0.3s steps(4);
			transition: all 0.3s steps(4);
			z-index: 1;
			bottom: 100%;
			top: 0;
		}
		&:before {
			left: 50%;
			transition-delay: 0.05s;
		}
		&:after {
			left: 75%;
			transition-delay: 0.15s;
		}
	}
	&:hover {
		&:before,
		&:after {
			bottom: 0;
			opacity: 0.5;
		}
		span {
			&:before,
			&:after {
				bottom: 0;
				opacity: 0.5;
			}
		}
	}
}
.blog-details {
	text-align: center;
	padding-left: 15px;
	padding-right: 15px;

	h4 {
		color: $theme-deafult;
		margin-top: 25px;
