		margin-bottom: 0;
	}
	p {
		color: $dark-font;
		font-size: 18px;
		text-transform: uppercase;
		font-weight: 700;
		line-height: 1.5;
		letter-spacing: 0.03em;
		margin-top: 10px;
		margin-bottom: -5px;
		transition: all 0.5s ease;
		&:hover {
			color: $theme-deafult;
			transition: all 0.5s ease;
		}
	}
	h6 {
		line-height: 1;
		letter-spacing: 0.1em;
		margin-bottom: 0;
	}
}
