		justify-content: center;
		padding-top: 40px;
		padding-bottom: 40px;
		position: relative;
		> div {
			&:after,
			&:before {
				content: "";
				width: 1px;
				height: 0;
				position: absolute;
				transition: all 0.2s linear;
				background: $theme-deafult;
				transition-delay: 0s;
			}
			&:before {
				left: 0;
				top: 0;
			}
			&:after {
				right: 0;
				bottom: 0;
			}
		}
		&:after,
		&:before {
			content: "";
			width: 0;
			height: 1px;
			position: absolute;
			transition: all 0.2s linear;
			background: $theme-deafult;
		}
		&:before {
			right: 0;
			top: 0;
		}
		&:after {
			left: 0;
			bottom: 0;
		}
		&:hover {
			transition: all 0.5s ease;
			&:after,
			&:before {
				transition-delay: 0s;
				width: 100%;
			}
			> div {
				&:after,
				&:before {
					transition-delay: 0.2s;
					height: 100%;
				}
