.tools-grey {
	.product-m {
		.slick-slide {
			.img-wrapper {
				margin: 0;
			}
		}
	}
	.product-box {
		width: 100%;
		display: inline-block;
		overflow: hidden;
		background-color: white;
		box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.05);
		margin: 0 10px;
		.img-wrapper {
			.lable-block {
				transition: all 0.5s ease;
			}
		}
		.cart-info {
			display: flex;
			left: 0;
			bottom: -30px;
			transition: all 0.5s ease;
			.mobile-quick-view {
				display: none;
			}
			a {
				background-color: #eef0f1;
				border-radius: 5px;
				i {
					color: $grey2;
				}
				&:hover {
					background-color: $theme-deafult;
					transition: all 0.5s ease;
					i {
						color: $white;
						transition: all 0.5s ease;
					}
				}
			}
			button {
				width: 55%;
				background-color: #eef0f1;
				color: $grey2;
				border-radius: 5px;
				margin: 0 5px;
				transition: all 0.5s ease;
				i {
					display: none;
				}
				&:hover {
					background-color: $theme-deafult;
					color: $white;
					transition: all 0.5s ease;
				}
			}
		}
		.quick-view-part {
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -20px;
			background-color: $theme-deafult;
			border-radius: 100%;
			width: 40px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			transform: translate(-50%, -50%);
			opacity: 0;
			transition: all 0.5s ease;
			i {
				font-size: 18px;
				color: $white;
			}
		}
		.product-info {
			padding-left: 15px;
			padding-bottom: 15px;
			background-color: $white;
			border-top: 1px solid rgba($round-border, 0.2);
			.rating {
				margin-top: 0;
				padding-top: 15px;
			}
		}
		.br {
			&.br-default {
				display: flex;
				justify-content: center;
			}
		}
		&:hover {
			.img-wrapper {
				.front {
					img {
						opacity: 0.05;
						transition: all 0.5s ease;
					}
				}
				.cart-info {
					opacity: 1;
					transition: all 0.5s ease;
					bottom: 10px;
					button {
						animation: none;
					}
					a {
						&:nth-child(2) i {
							animation: none;
						}
						&:nth-child(3) i {
							animation: none;
						}
						&:nth-child(4) i {
							animation: none;
						}
					}
				}
				.lable-block {
					display: none;
					transition: all 0.5s ease;
				}
			}
			.quick-view-part {
				opacity: 1;
				transition: all 0.5s ease;
			}
			.ribbon {
				display: none;
				transition: all 0.5s ease;
			}
		}
	}
	.ribbon {
		position: absolute;
		left: -5px;
		top: -5px;
		z-index: 1;
		overflow: hidden;
		width: 75px;
		height: 75px;
		text-align: right;
		transition: all 0.5s ease;
		span {
			font-size: 10px;
			font-weight: bold;
			color: #fff;
			text-transform: uppercase;
			text-align: center;
			line-height: 20px;
			transform: rotate(-45deg);
			width: 100px;
			display: block;
			background: $theme-deafult;
			box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
			position: absolute;
			top: 19px;
			left: -21px;
			&:before {
				content: "";
				position: absolute;
				left: 0px;
				top: 100%;
				z-index: -1;
				border-left: 3px solid $theme-deafult;
				border-right: 3px solid transparent;
				border-bottom: 3px solid transparent;
				border-top: 3px solid $theme-deafult;
			}
			&:after {
				content: "";
				position: absolute;
				right: 0px;
				top: 100%;
				z-index: -1;
				border-left: 3px solid transparent;
				border-right: 3px solid $theme-deafult;
				border-bottom: 3px solid transparent;
				border-top: 3px solid $theme-deafult;
			}
		}
	}
}
