				font-size: 22px;
				position: absolute;
				top: 0;
				right: -20px;
			}
		}
	}
	.demo-effects {
		margin-bottom: 30px;

		h4 {
			text-transform: capitalize;
		}
		.set-position {
			display: block;
		}
		> div {
			text-decoration: none;
			color: black;
			.layout-container {
				height: 110px;
				width: 100%;
				display: inline-flex;
				background-size: cover;
				transition: all 2s ease-out 0s;
				box-shadow: -1px 2px 5px 1px #ececec;
				@for $i from 1 through 70 {
					&.demo#{$i} {
						background-image: url("#{$assetspath}/landing-page/demo/#{$i}.jpg");
					}
				}
			}
			.demo-text {
				h4 {
					font-size: 16px;
					margin-top: 10px;
					margin-bottom: 0;
					color: #000000;
					span {
						font-weight: 500;
					}
				}
				.demo-btn {
					margin-top: 10px;
					.btn {
						border: 1px solid #ddd;
						background-color: white;
						width: 90px;
						color: $theme-deafult;
						padding: 5px 10px;
						border-radius: 30px;
						&:hover {
							background-color: $theme-deafult;
							color: white;
						}
					}
				}
			}
		}
		&:last-child {
			margin-bottom: 0;
		}
		&.effect-2 {
			.layout-container {
				background-size: cover;
				background-repeat: no-repeat;
			}
			&:hover {
				.layout-container {
					background-position: top !important;
				}
			}
		}
		&.effect-3 {
			.layout-container {
				background-size: cover;
				background-repeat: no-repeat;
			}
			&:hover {
				.layout-container {
					background-position: top !important;
				}
			}
		}
		&:hover {
			.layout-container {
				background-position: center 100% !important;
			}
		}
	}
}
@keyframes ring-rotate {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
.sec-position {
	position: relative;
	top: -80px;
}
.color-picker {
	position: fixed;
	right: -190px;
	top: 35%;
	width: 190px;
	z-index: 1;
	transition: all 0.3s ease;
	.settings-header {
		background: #f3f3f3;
		padding: 5px 15px;
		box-shadow: 0px -10px 20px -5px rgba(220, 220, 220, 0.62);
