			color: black;
			margin: 0;
			font-size: 20px;
			padding: 4px 9px;
		}
	}
	.section {
		background: white;
		padding: 10px;
		border: 1px solid #f3f3f3;
		overflow: auto;
		i {
			font-size: 16px;
			margin-right: 10px;
		}
	}
	a {
		&.handle {
			position: absolute;
			right: 190px;
			width: 42px;
			height: 41px;
			color: $black;
			transition: all ease-in 0.3s;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 22px;
			z-index: -1;
			border-top-left-radius: 50%;
			border-bottom-left-radius: 50%;
			text-decoration: none;
			background: #ffffff;
			top: -80px;
			border: 1px solid white;
			box-shadow: 0 0 5px rgba(4, 4, 4, 0.28);
			i {
				margin-left: 5px;
			}
			&:hover {
				color: $theme-deafult;
			}
		}
	}
	.colors {
		a {
			width: 32px;
			height: 32px;
			margin: 5px;
			float: left;
			transition: all ease-in 0.3s;
		}
	}
	.skin {
		a {
			display: block;
			width: 70px;
			height: 30px;
			margin-left: 0;
			margin-top: 0;
			margin-right: 5px;
			float: left;
			text-align: center;
			line-height: 30px;
			border: 2px transparent solid;
			&.actt {
				border: 2px white solid;
			}
		}
	}
}
.handle {
	i {
		animation: ring-rotate 3s infinite linear;
	}
	&:hover {
		background-color: white;
	}
}

