						transition: all 0.3s ease;
						.demo-section {
							.demo-effects {
								> div {
									.layout-container {
										box-shadow: none;
									}
									.demo-text {
										h4 {
											color: $white-3;
										}
										.demo-btn {
											.btn {
												border-color: $dark-border;
												background-color: $dark-body;
												transition: all 0.3s ease;
												color: $dark-link;
											}
										}
									}
								}
							}
							.title-text {
								h3 {
									color: $white-2;
								}
							}
						}
					}
				}
			}
		}
		&.cart-modal {
			.modal-dialog {
				.modal-content {
					.modal-body {
						.modal-bg {
							&.addtocart {
								#upsell_product {
									.product-box {
										.product-detail {
											h6 {
												a {
													color: $dark-link;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
		&.exit-modal {
			.media {
				.media-body {
					h5 {
						color: $white-4;
					}
				}
			}
			.stop {
				filter: invert(100);
			}
		}
	}
	#quick-view {
		.modal-dialog {
			.modal-content {
				.modal-body {
					background-color: $dark-body;
					transition: all 0.3s ease;
				}
			}
		}
	}
	.addcart_btm_popup {
		background-color: $dark-top;
		transition: all 0.3s ease;
		border-color: $dark-border;
		.fixed_cart {
			i {
				color: $dark-link;
			}
		}
	}
	.review-page {
		.comnt-sec {
			li {
				a {
					color: $dark-link;
				}
			}
		}
	}
	.sitemap_page {
		ul {
			li {
				a {
					color: $dark-link;
					&:hover {
						color: $theme-deafult;
					}
				}
			}
			ul {
				li {
					a {
						color: $white-4;
					}
				}
			}
		}
	}
	.effect-cls {
		&:before,
		&:after {
			filter: invert(100);
		}
	}
	.game-product {
		.product-box {
			.cart-info {
				a {
					i {
						background-color: $dark-body;
						transition: all 0.3s ease;
						color: $dark-link;
					}
				}
			}
		}
	}
	.demo-right {
		a {
			background-color: $black;
			transition: all 0.3s ease;
		}
	}
	.rtl-btn,
	.dark-light {
		background-color: $black;
		box-shadow: none;
	}
	.color-picker {
		a {
			&.handle {
				background-color: $black;
				transition: all 0.3s ease;
				color: $dark-link;
			}
		}
	}
	.setting-box {
		.setting-title {
			h4 {
				color: $font-color;
			}
		}
	}
}

/*=====================
    18.Portfoilio CSS start
==========================*/
.portfolio-padding {
	padding-bottom: 40px;
}
.portfolio-section {
	.article-title {
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 0;
		color: white;
		transition: all 0.5s ease;
		text-align: center;
		float: left;
		font-size: 18px;
		line-height: 24px;
		a {
			color: white;
		}
	}
	.isotopeSelector {
		float: left;
		width: 100%;
		position: relative;
		.overlay {
			position: relative;
			border: 1px solid $round-border;
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	.isotopeSelector {
		margin-bottom: 30px;
		img {
			transition: all 0.4s ease;
		}
		.overlay-background {
			transform: scale(0);
			transition: all 0.4s ease;
			i {
				position: absolute;
				top: 50%;
				left: 50%;
				background-color: white;
				padding: 10px;
				border-radius: 100%;
				transform: translate(-50%, -50%);
				color: $theme-deafult;
				font-size: 22px;
				display: none;
				transition: all 0.4s ease;
				width: 40px;
				height: 40px;
				align-items: center;
				justify-content: center;
			}
		}
		&:hover {
			img {
				transform: scale(1.2) rotate(2deg);
				transition: all 0.4s ease;
			}
			.overlay-background {
				transform: scale(1);
				border: 3px solid white;
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				z-index: 1;
				background-color: rgba($theme-deafult, 0.2);
				transition: all 0.4s ease;
				i {
					display: flex;
					transition: all 0.4s ease;
				}
			}
		}
	}
	.border-portfolio {
		margin: 3px;
		overflow: hidden;
	}
	#form1 {
		padding-bottom: 20px;
	}
	.filter-button {
		padding: 0 24px;
		font-size: 20px;
		line-height: 35px;
		border: none;
		background-color: white;
		&.active {
			background-color: $theme-deafult;
			color: $white;
		}
	}
	&.fullwidth-portfolio {
		.isotopeSelector {
			padding-left: 7px;
			padding-right: 7px;
			margin-bottom: 14px;
		}
	}
	&.metro-section {
		.isotopeSelector {
			&:hover {
				img {
					//transform: scale(1) rotate(0deg);
					//transition: all 0.5s ease;
				}
			}
		}
		.product-box {
			.product-detail {
				opacity: 0;
				position: absolute;
				background-color: $white;
				padding: 10px;
				transition: all 0.5s ease;
				width: 65%;
				left: 0;
				right: 0;
				margin: 0 auto;
				bottom: -10px;
				text-align: center;
				h6 {
					color: #525252;
					padding-top: 0;
					margin-top: -2px;
				}
				h4 {
					font-weight: 400;
					color: $black;
				}
			}
			.cart-wrap {
				top: 7px;
				right: 7px;
				opacity: 1;
				button {
					border: 1px solid $white;
					border-radius: 100%;
					background-color: rgba($white, 0.8);
					opacity: 1;
					i {
						padding: 7px;
						color: $font-color;
					}
				}
				a {
					i {
						border-radius: 100%;
						border: 1px solid $white;
						margin: 5px 0;
						background-color: rgba($white, 0.8);
						padding: 7px;
						color: $font-color;
						opacity: 0;
						display: flex;
						align-items: center;
						justify-content: center;
					}
				}
			}
			&:hover {
				.product-detail {
					opacity: 1;
					transition: all 0.5s ease;
					bottom: 15px;
				}
				.cart-wrap {
					button {
						animation: none;
					}
					a {
						i {
							opacity: 1;
						}
						&:nth-child(2) {
							i {
								animation: fadeInDown 500ms ease-in-out;
							}
						}
						&:nth-child(3) {
							i {
								animation: fadeInDown 700ms ease-in-out;
							}
						}
						&:nth-child(4) {
							i {
								animation: fadeInDown 1000ms ease-in-out;
							}
						}
					}
				}
			}
		}
	}
}
.filter-section {
	.filter-container {
		text-align: center;
		padding: 0 0 20px;
		ul {
			&.filter {
				> li {
					padding: 0 25px;
					> a {
						color: $theme-deafult;
						font-size: 20px;
						line-height: 40px;
						&:hover,
						&:focus {
							text-decoration: none;
							color: $theme-deafult;
							outline: none;
						}
					}
					&.active {
						background-color: $theme-deafult;
						a {
							color: $white;
						}
					}
					span {
						color: #ddd;
						font-size: 20px;
						display: inline-block;
						margin: 0 5px;
					}
				}
			}
		}
		.filter {
			> li {
				padding: 0;
				margin: 0;
			}
		}
	}
	.title1 {
		.title-inner1 {
			margin-top: -5px;
		}
	}
	h1 {
		text-align: center;
	}
}
.demo-colored-wrap {
	background: #ecf0f1;
}
.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
	opacity: 0;
	-webkit-backface-visibility: hidden;
	-webkit-transition: all 0.3s ease-out;
	-moz-transition: all 0.3s ease-out;
	-o-transition: all 0.3s ease-out;
	transition: all 0.3s ease-out;
}
.mfp-with-zoom.mfp-ready .mfp-container {
	opacity: 1;
}
.mfp-with-zoom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
	opacity: 0;
}
.mfp-title {
	display: none;
}
.offer-box {
	top: 50%;
	position: fixed;
	transform: translateY(-50%);
	display: flex;
	left: -415px;
	transition: all 0.5s ease;
	z-index: 9;
	img {
		margin-right: 18px;
	}
	&.toggle-cls {
		left: 0;
		transition: all 0.5s ease;
	}
	.heading-right {
		h3 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 230px;
			height: 36px;
			line-height: 36px;
			letter-spacing: 5px;
			font-size: 18px;
			color: #ffffff;
			font-weight: 700;
			text-transform: uppercase;
			transform: translate(50%, -50%) rotate(-90deg);
			text-align: center;
			background-color: $theme-deafult;
			margin-bottom: 0;
			cursor: pointer;
		}
	}
}

.my-masonry-grid {
	display: -webkit-box; /* Not needed if autoprefixing */
	display: -ms-flexbox; /* Not needed if autoprefixing */
	display: flex;
	margin-left: -30px; /* gutter size offset */
	width: 100%;
}
.my-masonry-grid_column {
	padding-left: 30px; /* gutter size */
	background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div {
	/* change div to reference your elements you put in <Masonry> */
	margin-bottom: 30px;
}
